import React from 'react';
import { CollectionEmptyState } from '@wix/patterns';
import { usePatternsWizardOverridesContext } from '../providers';
import { EmptyState } from '../types';

export const useEmptyStates = (emptyState?: EmptyState) => {
  const { title, subtitle } = emptyState || {};

  const overrides = usePatternsWizardOverridesContext();

  const onClick = emptyState?.addNewCta?.id
    ? overrides?.emptyState?.[emptyState?.addNewCta?.id]
    : undefined;
  const addNewCta = onClick
    ? {
        addNewCta: {
          ...(emptyState?.addNewCta?.text
            ? { text: emptyState?.addNewCta?.text }
            : {}),
          onClick,
        },
      }
    : {};

  const _customCta = emptyState?.customCta?.id
    ? overrides?.emptyState?.[emptyState?.customCta?.id]
    : undefined;

  const customCta = _customCta
    ? {
        customCta: _customCta(),
      }
    : {};

  const _image = emptyState?.image?.id
    ? overrides?.emptyState?.[emptyState?.image?.id]
    : undefined;

  const image = _image
    ? {
        image: _image(),
      }
    : {};

  return {
    emptyState: (
      <CollectionEmptyState
        title={title}
        subtitle={subtitle}
        {...addNewCta}
        {...customCta}
        {...image}
      />
    ),
  };
};
